import React, {useState} from 'react';
import Countdown from '../Countdown/Countdown';
import Button from '../../components/UI/Button/Button';
import TargetSwitcher from './TargetSwitcher/TargetSwitcher';

const RangeTest = props => {

    const [isReady, setIsReady] = useState(false);
    const [isCountDown, setIsCountdown] = useState(true);
    const TARGETS_QUANTITY = 10;

    const countdownEndHandler = () => {
        setIsCountdown(false);
    };

    let content = (
        <div>
            <h3>Ready?</h3>
            <Button clicked={() => setIsReady(true)}>START</Button>
        </div>
    );

    if(isReady){
        content = 
            <div>
                <br/>
                <Countdown time={3} countdownEnd={countdownEndHandler} text="skip"/>
            </div>
    }

    if (!isCountDown) {
        let ranges = [];
        for(let i = 0; i < TARGETS_QUANTITY; i++){
            ranges.push(Math.floor(Math.random() * 11) * 50 + 200);
        }


        let fields = [];
        for(let i = 0; i < TARGETS_QUANTITY; i++){
            if(ranges[i] >= 500)
                fields.push(Math.floor(Math.random() * 3));
            else if(ranges[i] <= 300)
                fields.push(Math.floor(Math.random() * 3) + 6);
            else
                fields.push(Math.floor(Math.random() * 3) + 3);
        }

        content = (
            <div>
                <TargetSwitcher ranges={ranges} fields={fields}/>
            </div>
        );

    }

    return (content);
}

export default RangeTest;