import React, {useState} from 'react';
import Input from '../../components/UI/Input/Input';
import Button from '../../components/UI/Button/Button';
import {Link} from 'react-router-dom';
import classes from './Form.module.css';

const Form = props => {
    const [enteredUsername, setEnteredUsername] = useState('');
    const [enteredPassword, setEnteredPassword] = useState('');

    const onChangeHandler = (event, labelName) => {
        switch (labelName) {
            case 'userName':
                setEnteredUsername(event.target.value);
                break;
            case 'password':
                setEnteredPassword(event.target.value);
                break;
            default:
                break;
        }
    };

    let form = (
        <div>
            <Input label="User Name:" value={enteredUsername} type='text' inputOnChangeHandler={(event) => onChangeHandler(event, 'userName')}/>
            <Input label="Password:" value={enteredPassword} type='password' inputOnChangeHandler={(event) => onChangeHandler(event, 'password')}/>
            <Button>Login</Button>
            <Link className={classes.Link} to='/signin'>Sign in</Link>
        </div>
    );

    if (!props.loggedIn){
        form = (
            <div>
                <Link className={classes.Link}>Logout</Link>
            </div>
        );
    }
    
    return (
        <div>
            {form}
        </div>
    );
};

export default Form;