import React, {useState, useCallback, useRef} from 'react';
import classes from './TargetSwitcher.module.css';
import RangeSlider from './RangeSlider/RangeSlider';
import Countdown from '../../Countdown/Countdown';
import Scope from './Scope/Scope';


const TargetSwitcher = props => {

    const DEFAULT_RANGE_VAL = 450;
    const RANGES_STEPS = 50;
    const MIN_RANGE = 200;
    const MAX_RANGE = 700;
    const COUNTDOWN_TIME = 12;
    const END_ROUND_DELAY_TIME = 1;
    const DEFAULT_RETICLE = "mildot";

    const guessedRanges = useRef([]);

    const [round, setRound] = useState(0);
    const [estimatedRange, setEstimatedRange] = useState(DEFAULT_RANGE_VAL);
    const [endEstimate, setEndEstimate] = useState(false);
    const [reticle, setReticle] = useState(DEFAULT_RETICLE);


    const calculateScore = (ranges, guesses) => {

        const ALL_RANGES_QUANTITY = (MAX_RANGE - MIN_RANGE) / RANGES_STEPS + 1;
        const length = guesses.length;
        let score = 0;

        for(let i = 0; i < length; i++){
            let gap = Math.abs(guesses[i] / RANGES_STEPS - ranges[i] / RANGES_STEPS);
            score += gap / length;
        }

        score = Math.round((1 - score/ALL_RANGES_QUANTITY) * 100) + '/100';

        return score;
    };

    const onReticleChangesHandler = reticle => {
        setReticle(reticle);
    };

    const endCountdownHandler = useCallback(() => {
        setEndEstimate(true)
    }, []);

    const endRoundHandler = () => {
        guessedRanges.current.push(estimatedRange);
        setRound((prevRound) => (prevRound + 1));
        setEndEstimate(false);
        setEstimatedRange(DEFAULT_RANGE_VAL);
    }


    let content = endEstimate 
        ?
        <React.Fragment>
            <h2 className={classes.realRange}>{props.ranges[round]} m</h2>
            <div className={classes.endDelay}><Countdown time={END_ROUND_DELAY_TIME} countdownEnd={endRoundHandler} text=""/></div>
        </React.Fragment>
        :
        <React.Fragment>
            <Countdown time={COUNTDOWN_TIME} countdownEnd={endCountdownHandler} text="continue"/>
            <RangeSlider min={MIN_RANGE} max={MAX_RANGE} step={RANGES_STEPS} rangeValue={estimatedRange} onChange={range => setEstimatedRange(range)}/>
        </React.Fragment>;

    let roundAndScope = endEstimate 
    ? <div className={classes.round}>Round {round+1}/{props.ranges.length}</div>
    : (
        <React.Fragment>
            <div className={classes.round}>Round {round+1}/{props.ranges.length}</div>
            <Scope range={props.ranges[round]} field={props.fields[round]} reticle={reticle} onReticleChangesHandler={onReticleChangesHandler}/>
        </React.Fragment>
    );

    let score = null;

    if(round === props.ranges.length){
        score = <React.Fragment>
                    <h2>Your Score Is:</h2>
                    <h2>{calculateScore(props.ranges, guessedRanges.current)}</h2>
                </React.Fragment>;
        content = null;
        roundAndScope = null;
    }


    return(
        <div className={classes.TargetSwitcher}>
            {roundAndScope}
            {content}
            {score}
        </div>
    );



}

export default TargetSwitcher;